.avatar-img{
  height: 450px;
  padding-top: 3em;

}

.banner-text{
  background-color: white;
  width :75%;
  margin:auto;
  border-radius:10px;
  padding-top: 1em;
  height: 400px;
  opacity: 92%;
}

.banner-text h1{
  font-size: 25px;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin-top: 0px;
  margin-bottom: 0px;
}

.banner-text h1 span{
  font-size: 50px;
}

.banner-text h4{
  font-size: 13px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}

.banner-text hr{
  border-top: 2px dotted black;
  text-align: center;
  width:98%;
  margin-bottom: 1em;
  
}

.icon {
  display: flex;
  justify-content: space-around;
  margin: auto;

}

.contact{

  margin-left: 50px;
}

body{
  height: 80vh;
}